<template>
  <div class="team">
    <div class="header flex justify-center items-center">
      <p class=" text-white text-font-32 font-weight-bold mt-20">精英团队</p>
    </div>
    <div class="body mx-auto py-4">
      <swiper class="swiper " :options="swiperOption">
        <swiper-slide
          v-for="(item, index) in list"
          :key="'swipe' + index"
          class="flex items-center"
        >
          <div
            class=" bg-image w-full bg-no-repeat bg-cover bg-center bg-top"
            :style="{ backgroundImage: 'url(' + item.image + ')' }"
          >
            <div class="profile ">
              <div class="icon-image flex justify-center items-center">
                <img src="../../assets/images/team/arrow-up.png" alt="" />
              </div>

              <div class="flex  items-end justify-center pt-6">
                <p class=" text-font-18 text-white font-bold mr-3 ">
                  {{ item.name }}
                </p>
                <p class=" text-font-14  text-brighter">
                  {{ item.title }}
                </p>
              </div>
              <div class="py-4 text-white px-6 profile-desc">
                <p class=" text-center">
                  {{ item.desc }}
                </p>
              </div>
            </div>
          </div>
        </swiper-slide>
        <div class="swiper-pagination" slot="pagination"></div>
      </swiper>
    </div>
  </div>
</template>

<script>
import { swiper, swiperSlide } from 'vue-awesome-swiper'
import 'swiper/css/swiper.css'
import { getCategory } from '@/api'

export default {
  components: {
    swiper,
    swiperSlide
  },
  created() {
    getCategory(null, 3).then(ret => {
      if (ret.result) {
        this.list = ret.result.map(c => {
          return {
            image: c.bg.md + '?x-oss-process=image/resize,h_550',
            name: c.name,
            title: c.title.zh,
            desc: c.content.zh
          }
        })
      }
    })
  },
  data() {
    return {
      list: [],
      swiperOption: {
        slidesPerView: 4,
        spaceBetween: 0,
        loop: true,
        autoplay: {
          delay: 8000,
          stopOnLastSlide: false,
          disableOnInteraction: false
        }
      }
    }
  }
}
</script>

